<template>
  <div class="mealLayout">
    <div class="popup-con">
      <div class="main-con">
        <div class="close-btn" @click="closeClick">
          <Icon type="close-round" size="20" color="#999"></Icon>
        </div>
        <div class="body-con">
          <div class="title">升级多门店管理</div>
          <div class="choose">
            <div class="label">选择门店套餐</div>
            <Select style="width: 100%" v-model="selected">
              <Option v-for="item in optionList " :value="item.value" :key="item.label">{{item.label}}</Option>
            </Select>
            <p>你将会在试用期结束之后收到账单结算</p>
          </div>
          <div class="save-btn" @click="upgrade(item)">升级套餐</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        optionList:[{label:"10门店 - 10880元/年",value:"10"},{label:"30门店 - 14880元/年",value:"30"}],
        selected:"10",
      }
    },
    methods:{
      upgrade(){
        let arr = this.optionList.filter(item => item.value === this.selected);
          this.post("custom/Hotel/stores_up",{
            set_meal: arr[0].label,
            number: this.selected
          }).then(res =>{
              if(res.data.code === 10000){
                this.$emit('popupShow','mealShow')
                this.$Modal.success({
                  title: "升级成功",
                  content: res.data.msg
                });
              }else{
                this.$Modal.error({
                  title: "升级失败",
                  content: res.data.msg
                });
              }
          })
      },
      closeClick(){
        this.$emit('popupShow','mealShow')
      }
    }
  }
</script>

<style scoped>
  .mealLayout {
    position: fixed;
    overflow: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1002;
    outline: 0;
    white-space: nowrap;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .main-con {
    position: relative;
    background-color: #fff;
    border: 0;
    border-radius: 4px;
    background-clip: padding-box;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    min-width: 400px;
  }

  .close-btn {
    cursor: pointer;
    border: 0;
    background: transparent;
    position: absolute;
    top: 30px;
    right: 30px;
    z-index: 10;
    text-decoration: none;
  }

  .body-con {
    padding: 28px 30px;
    font-size: 12px;
    line-height: 1.5;
  }

  .body-con .title {
    text-align: center;
    font-size: 20px;
    color: #4b5056;
  }

  .body-con .choose {
    margin: 30px 0;
  }

  .body-con .choose .label {
    color: #3d3d41;
    font-weight: 700;
    margin-bottom: 15px;
  }

  .choose p {
    color: #636972;
    line-height: 1.4;
    margin-top: 15px;
  }

  .save-btn {
    width: 100%;
    padding: 10px 0;
    background: #ecd500;
    border: 1px solid #ecd500;
    color: #353120;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    border-radius: 3px;
    font-weight: 700;
    text-align: center;
  }

  .save-btn:hover {
    cursor: pointer;
    background: #ffef00;
    border-color: #ffef00;
  }
</style>



