<template>
  <div class="popupLayout">
    <div class="popup-con">
      <div class="popup-main">
          <div class="main-con">
              <div class="main-body">
                <div class="body-title">
                  <span>{{currentType.name}}</span>
                  <div class="icon" @click="popupDis">
                    <Icon type="close-round" color="#666" ></Icon>
                </div>
                </div>
                <div class="input-item">
                  <div class="input-title">
                    <span>门店名称</span>
                    <div class="errorMsg">{{errorObj.nameMsg}}</div>
                  </div>
                  <input type="text" name="name" placeholder="请输入门店名称" v-model="hotelName" @blur="inputBlur('nameMsg')" @input="inputChange('nameMsg')" @focus="inputFocus('hotelName')" ref="hotelName">
                </div>
                <div class="input-item">
                  <div class="input-title">
                    <span>门店地址</span>
                    <div class="errorMsg">{{errorObj.addressMsg}}</div>
                  </div>
                  <input type="text" name="address" placeholder="请输入省市区详细地址" v-model="hotelAddress" @blur="inputBlur('addressMsg')" @input="inputChange('addressMsg')" @focus="inputFocus('hotelAddress')" ref="hotelAddress">
                </div>
                <div class="input-item">
                  <div class="input-title">
                    <span>联系电话</span>
                    <div class="errorMsg">{{errorObj.phoneMsg}}</div>
                  </div>
                  <input type="text" name="phone" placeholder="请输入门店联系电话" v-model="hotelPhone" @blur="inputBlur('phoneMsg')" @input="inputChange('phoneMsg')" @focus="inputFocus('hotelPhone')" ref="hotelPhone">
                </div>
                <div class="sure-btn" @click="save(currentType.id)" v-if="load">
                  保存
                </div>
                <div class="sure-btn" v-if="!load" >
                  保存
                  <img src="../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%">
                </div>
              </div>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
  export  default {
    data(){
      return {
        load:true,
        hotelName:"",
        hotelAddress:"",
        hotelPhone:"",
        errorObj:{
          nameMsg:"",
          addressMsg:"",
          phoneMsg:""
        }
      }
    },
    props:["currentType"],
    watch:{
    },
    methods:{
//        获取某个信息
      getSomeMeal(){

      },
      popupDis(){
        this.$emit('popupShow','storeShow')
      },
      inputBlur(value){
        let obj = this.validation(value);
        if(this[obj.name]===""){
          this.$refs[obj.name].style.borderColor = '#e64751';
          this.errorObj[obj.error]= obj.text
        }
      },
      inputChange(value){
        let obj = this.validation(value);
        this.$refs[obj.name].style.borderColor = '#c6c9cd';
        this.errorObj[obj.error] = ''
      },
      inputFocus(value){
        if(value){
          this.$refs[value].style.borderColor='skyblue'
        }
      },
//      保存信息
      saveMeal(url,id){
        this.load = false;
        this.post(url, {
          stores_name:this.hotelName,
          stores_address:this.hotelAddress,
          stores_tell:this.hotelPhone,
          id
        }).then(res => {
          this.load = true;
          if (res.data.code === 10000){
            this.$parent.storeShow = false;
            this.$parent.maskShow = false;
            this.$Modal.success({
              title: "添加成功",
              content: res.data.msg
            });
            this.$emit("updateMeal","update");
          }else{
            this.$Modal.success({
              title: "添加失败",
              content: res.data.msg
            });
          }
        })
      },
      save(val){
         if(val){
//             修改
            this.saveMeal("custom/Hotel/update_stores ",val)
         }else{
//             保存
           this.saveMeal("custom/Hotel/add_stores")
         }
       },
      validation(value){
        let name,error,text;
         switch (value){
           case 'nameMsg':
             name = 'hotelName';
             error = 'nameMsg';
             text = '名称不能为空字符串';
             break;
           case 'addressMsg':
             name = 'hotelAddress';
             error = 'addressMsg';
             text = '请输入门店详细地址';
             break;
           case 'phoneMsg':
             name = 'hotelPhone';
             error = 'phoneMsg';
             text = '请输入门店联系电话';
             break;
           default:
             break
         }
        return {name,error,text}
      }
    },
    mounted(){
        if(this.currentType["id"]){
          this.post("custom/Hotel/get_stores_detail",{
            id:this.currentType["id"]
          }).then(res=>{
             if(res.data.code === 10000){
              this.hotelName = res.data.data.stores_name;
              this.hotelAddress = res.data.data.stores_address;
              this.hotelPhone = res.data.data.stores_tell;
             }
          })
        }else{
          return false;
      }
    }
  }
</script>

<style scoped>
  .popupLayout{
    position: fixed;
    overflow: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1002;
    outline: 0;
    white-space: nowrap;
    display: flex;
    align-items: center;
    justify-content: center;
  }
.popup-main{
  min-width: 400px;
  color: #636972;
  font-size: 15px;
  text-align: left;
  position: relative;
  background-color: #fff;
  border: 0;
  border-radius: 4px;
  background-clip: padding-box;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}
  .main-con{padding: 28px 30px;}
  .main-body .body-title{
    display: flex;
    align-items: center;
   justify-content: space-between;
  }
.main-body .body-title span{
  font-size: 20px;
  color: #4b5056;
  display: inline-block;
}
  .input-item{margin-top: 15px;}
  .input-item .input-title{
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    padding: 8px 0;
    align-items: center;
  }
.input-item .input-title span{
  color: #52616a;
  font-weight: 600;
}
.input-item .input-title .errorMsg{
  color: #e64751;
  font-size: 12px;
}
  .input-item input{
    border: 1px solid #c6c9cd;
    width: 100%;
    border-radius: 3px;
    box-shadow: inset 0 1px 2px rgba(0,0,0,.1);
    color: #636972;
    line-height: 22px;
    padding: 5px 8px;
  }
.input-item input::placeholder{
  color: #999;
  font-size: 14px;
}
  .sure-btn{
    width: 100%;
    cursor: pointer;
    border-radius: 4px;
    transition: all 0.15s;
    padding: 10px 0;
    border: 1px solid #93b719;
    font-size: 16px;
    font-weight: 500;
    height: 40px;
    line-height: 22px;
    margin: 23px 0 20px;
    background: #93b719;
    text-align: center;
    color: #fff;
  }
.sure-btn:hover{
  cursor: pointer;
  background: #a5cd1c;
  border-color: #a5cd1c;
}
</style>



