<template>
  <div class="storeLayout">
    <header>
      <div class="header-one">
        <Icon type="transgender" size="25"></Icon>
        <span>多门店管理</span>
      </div>
      <p>主要面向线下连锁型企业的管理工具，为拥有多个门店的商家提供分门店线上管理。</p>
    </header>
    <div class="store-con">
      <div class="addCenter" v-if="storeList.length == 0">
        <Icon type="ios-home-outline" size="50"></Icon>
        <p>需要先创建门店哦</p>
        <a href="JavaScript:void(0)" @click="add" class="icon">
          <Icon type="plus-round" color="#fff"></Icon>
          <span>添加门店</span>
        </a>
      </div>
       <div class="add_server" v-else>
         <div class="add-btn" v-if="meal">
           <a href="JavaScript:void(0)" @click="add" class="icon">
             <Icon type="plus-round" color="#fff"></Icon>
             <span>添加门店</span>
           </a>
           <span class="tip">你还可以添加{{meal}}家门店</span>
           <a href="JavaScript:void(0)" @click="addMealClick" style="margin-left: 0.2rem;font-size: 0.14rem;color: #1BB0E6;">调整套餐</a>
         </div>
         <div class="add-btn update-btn" v-else>
           <a href="JavaScript:void(0)" @click="addMealClick" class="icon">
             <span>升级添加更多门店</span>
           </a>
           <span class="tip">升级后可添加更多门店</span>
         </div>
         <div class="list-con">
           <div class="list-common list-title">
             <div class="name">名称</div>
             <div class="address">门店地址</div>
             <div class="store-phone">联系电话</div>
             <div class="operate">操作</div>
           </div>
           <div class="con-list">
             <div class="list-common listItem" v-for="(item,index) in storeList" :key="index" >
               <div class="name">{{item.stores_name}}</div>
               <div class="address">{{item.stores_address}}</div>
               <div class="store-phone">{{item.stores_tell}}</div>
               <div class="operate">
                 <div class="operateCon">
                    <span class="iconfont icon-bianji" @click="add('current',index,item)"></span>
                    <span class="iconfont icon-lajitong"  @click="deteleMeal(item,index)" style="margin-left: 0.2rem;"></span>
                    <!--<Poptip placement="top" width="150" v-model="item.popup">
                      <div class="setting"><Icon type="gear-a" size="13" color="#a9aeb2"></Icon></div>
                      <div slot="content">
                        <div class="content-item" @click="add('current',index,item)">
                          <Icon type="gear-a" size="13" color="#a9aeb2"></Icon>
                          <span>修改门店</span>
                        </div>
                        <div class="content-item" @click="deteleMeal(item,index)">
                          <Icon type="trash-a" size="13" color="#a9aeb2"></Icon>
                          <span>删除门店</span>
                        </div>
                      </div>
                    </Poptip>-->
                 </div>
               </div>
             </div>
           </div>
         </div>
       </div>
    </div>
    <!--升级套餐提示区域-->
    <div class="update-tip-con" v-if="!storeList">
       <div class="main-con">
        <div class="img-box">
          <img src="../../../static/image/multiple-store-demo.png" alt="">
        </div>
         <div class="img-msg">
           <div class="title">添加多门店</div>
           <ul>
             <li>搜索结果涵盖所有门店，旅客可自由选择预订</li>
             <li>各门店可共享或独立管理房间数据</li>
           </ul>
         </div>
       </div>
    </div>
    <div class="popup-mask" v-if="maskShow"></div>
    <transition name="popupSlide">
      <addStore v-if="storeShow" @popupShow = 'popupShow' :currentType="currentType" @updateMeal="update"></addStore>
    </transition>
    <transition name="popupSlide">
      <addMeal v-if="mealShow" @popupShow = 'popupShow'></addMeal>
    </transition>
  </div>
</template>
<script>
  import addStore from './addStore.vue'
  import addMeal from './addMeal.vue'
export default {
  data(){
    return {
      storeList:[],
      storeShow:false, // 弹窗显示
      maskShow:false,
      currentType:"添加门店",
      isUpdate:false,//  升级添加更多套餐
      mealShow:false, //升级 套餐 的弹窗,
//    套餐详情
      meal:''
    }
  },
  methods:{

//      更新数据
    update(val){
        this.getMealList();
    },
//  删除门店
    deteleMeal(item,index){

//  删除门店
      this.$Modal.confirm({
          title: '删除门店',
          content: '<p>该门店下的房间、价格调整相关设置将被一同移除，且不可恢复。是否删除?</p>',
          onOk: () => {
            this.post("custom/Hotel/delete_stores", {
              id: item.id,
            }).then(res => {
              if(res.data.code === 10000) {
                this.$Modal.success({
                  title: "删除成功",
                  content: res.data.msg
                });
                this.storeList.splice(index,1);
              }else{
                this.$Modal.success({
                  title: "删除失败",
                  content: res.data.msg
                });
              }
            })
          },
          onCancel: () => {

          }
        });
    },
    addMealClick(){
      this.maskShow = true;
      this.mealShow = true
    },
    add(value,index,item){
              if(value && value==='current'){
                let mealInfo = {};
                mealInfo["name"] = "修改门店";
                mealInfo["id"] = item.id;
                this.currentType= mealInfo;
                this.storeList[index].popup = false;
                this.maskShow = true;
                this.storeShow = true
              } else{
                if(this.meal) {
                      if(this.meal.surplus_number !== 0 ){
                      let mealInfo = {};
                      mealInfo["name"] = "添加门店";
                      this.currentType = mealInfo;
                      this.maskShow = true;
                      this.storeShow = true
                    }else{
                        this.$Modal.error({
                          title: "友情提醒",
                          content: `最多可添加${this.meal.number}个门店`
                        });
                      }
                  }
            }
    },
    popupShow(value){
      this[value] = false;
      setTimeout(()=>(this.maskShow = false),800)
    },
//    获取数据
    getMealList(){
      this.post('custom/Hotel/list_stores', {
      }).then(res => {
        if(res.data.code === 10000){
          this.storeList = res.data.data;
          this.meal = res.data.number.surplus_number;
        }
      })
    }
  },
  mounted(){
      this.getMealList();
  },
  components:{addStore,addMeal}
}
</script>
<style scoped>
  /*  下方 升级提示区域*/
  .update-tip-con{
    width: 100%;
    display: inline-block;
  }
  .update-tip-con .main-con{
    padding: 30px;
    display: flex;
    align-items: center;
  }
  .main-con .img-box{
   width: 300px;
  }
  .main-con .img-box img{
    width: 100%;
  }
  .main-con .img-msg .title{
    font-weight: 600;
    font-size: 15px;
  }
  .main-con .img-msg{
    margin-left: 20px;
  }
  .main-con .img-msg ul li{
    list-style: disc;
    margin-top: 10px;
  }
  .popupSlide-enter-active{
    transition:all 0.8s ease;
    transform: translateY(0);
    opacity: 1;
  }
  .popupSlide-enter{
    opacity: 0;
    transform: translateY(-600px);
  }
  .popupSlide-leave{
    opacity: 1;
    transform: translateY(0);
  }
  .popupSlide-leave-active{
    transition: all 0.8s ease;
    opacity: 0;
    transform: translateY(500px);
  }
  .popup-mask{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    height: 100%;
    z-index: 1001;
  }
  header {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
    display: inline-block!important;
    width: 100%;
  }
  header .header-one{display: flex;}
  header i{
    margin-right: 10px;
  }
  .header-one span {
    font-size: 19px
  }
  header>p{
    color: #636972;
    font-size: 14px;
    line-height: 1.4;
    margin-top: 5px;
  }
  .addCenter{
    margin-top: 80px;
    text-align: center;
  }
  .addCenter>p:nth-child(2){
    margin: 0.1rem 0 0.05rem 0;
    font-size: 18px;
    font-weight: 800;
  }
 .store-con .icon{
    display: inline-block;
    padding: 8px 15px;
    background: #59A2ED;
    color: white;
    font-size: 0.14rem;
    border-radius: 0.05rem
  }
  .add_server .list-con{
    margin: 15px 0;
    font-size: 14px;
  }
  .add_server .add-btn{
    padding:  20px 0;
  }
  .add-btn .tip{
    color: #a5a6aa;
    margin-left: 10px;
  }
  .store-con .update-btn .icon{
    background: #ecd500;
    border: 1px solid #ecd500;
    color: #353120;
    font-weight: 600;
  }
  .store-con .update-btn .icon:hover{
    background: #ffef00;
    border-color: #ffef00;
  }
  .add_server .list-con .list-title{
    color: #636972;
    font-weight: 600;
    text-align: left;
    padding: 10px 0;
    text-align: center;
    background: #F9FAFB;
    border-radius: 3px 0 0 3px;
    /*border: 1px solid #d8d9db;*/
  }
  .list-common{
    width: 100%;
    display: flex;
    text-align: center;
  }
  .list-common .name{width:19%;padding-left: 1%;}
  .list-common .address{width: 30%;}
  .list-common .store-phone{width: 30%}
  .list-common .operate{width: 20%}
  .list-con .listItem{
    padding: 12px 0;
    border-bottom: 1px solid #eee;
    transition:  all 0.3s ease;
    align-items: center;
  }

  .operateCon{
    /*display: flex;*/
    /*align-items: center;*/
    text-align: center;
  }
  .operateCon .btn{
    background: #f6f6f6; color: #919394;font-size: 12px;  padding: 8px 12px;
    margin: 3px; transition: all 0.15s;text-decoration: none;font-weight: bold;  -webkit-appearance: none;border-radius: 4px;cursor: pointer;
    text-transform: uppercase;min-width: 80px;display: inline-block;text-align: center;border: 1px solid #ccc;line-height: 1;
  }
  .operateCon .btn:hover{background-color: #eee;border-color: #ccc;}
  .operateCon .setting{
    background-color: #f6f6f6;
    border: 1px solid #ccc;
    border-radius: 3px;
    box-shadow: inset 0 2px 0 0 hsla(0,0%,100%,.76);
    height: 26px;
    padding: 2px 4px;
    position: relative;
    width: 26px;
    text-align: center;
    margin-left: 10px;
  }
  .operateCon .content-item{
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  .operateCon .content-item span{
    font-size: 14px;color: #919394;display: inline-block;margin-left: 10px;font-weight: 700;
  }
  .con-list{
    background: white;
  }
</style>



